export default "<template>\n  <div>\n    <div style=\"display:flex;\">\n      <fd-calendar mode=\"range\" v-model=\"date\" />\n      <fd-calendar mode=\"range\" v-model=\"date\" />\n    </div>\n    <p><strong>From:</strong> {{ date.from }}</p>\n    <p><strong>To:</strong> {{ date.to }}</p>\n  </div>\n</template>\n\n<script>\nexport default {\n  data: () => ({\n    date: {\n      from: null,\n      to: null\n    }\n  })\n};\n</script>"