export default function (Component) {
Component.options.__tip = "<p>If you customize weekday names by using the <code>day-names</code>-prop you do that independently from <code>first-day-of-week</code>. This means that even though you may have set a different <code>first-day-of-week</code>, the first name specified in <code>day-names</code> is always and will always be the name for <strong>Sunday</strong>.</p>\n"
}